interface InterfaceProps {
    email?: string;
    error?: any;
    history?: any;
    password1?: string;
    password2?: string;
}

interface InterfaceState {
    login: string;
    email: string;
    error: any;
    password1: string;
    password2: string;
    avatar:string|null|ArrayBuffer;
    tel:string;
    name:string;
    surname:string;
    patronymic:string
}

export class SignUpForm extends React.Component<
    InterfaceProps,
    InterfaceState
    > {
    private static INITIAL_STATE = {
        login: "",
        error: null,
        email: "",
        password1: "",
        password2: "",
        avatar:"",
        tel:"",
        name:"",
        surname:"",
        patronymic:"",
    };

    private static propKey(propertyName: string, value: any): object {
        return { [propertyName]: value };
    }

    constructor(props: InterfaceProps) {
        super(props);

        this.state = { ...SignUpForm.INITIAL_STATE };
    }


    async HandleFormSubmit(event: any){
        const localState:any=this.state
        event.preventDefault();
        await Auth.register(localState.login,localState.email, localState.password1,localState.avatar,
            localState.tel, localState.name,localState.surname,localState.patronymic);
        localStorage.setItem('path','/article/subscribe')
        localStorage.setItem('rubrics','Ð’Ð°ÑˆÐ° Ð»ÐµÐ½Ñ‚Ð°')
        this.props.history.replace(HOME);
        window.location.reload();
    }

    public onSubmit = (event: any) => {
        const { login, password1 } = this.state;


        const { history } = this.props;


        event.preventDefault();
    };

    public render() {
        const { login, password1, password2, email, error } = this.state;

        const isInvalid = password1 === "" || password2 === "" ||login === "" || email === "" || password1!==password2;

        return (
            <>
            <div className={'container my-5'}>
                <div className={'row justify-content-md-center'}>
                    <div className={'col-md-3'}>
                    </div>
                    <div className="Article col-md-6">
                        <h3 className={"text-center"}>Ð ÐµÐ³Ð¸ÑÑ‚Ñ€Ð°Ñ†Ð¸Ñ</h3>
                        <Form onSubmit={event => this.HandleFormSubmit(event)}>

                            <Form.Group controlId="formBasicEmail">
                                <Form.Control type="email" placeholder="email"  onChange={event => this.setStateWithEvent(event, "email")}/>
                            </Form.Group>
                            <Form.Group controlId="formBasicLogin">
                                <Form.Control type="text" placeholder="Ð›Ð¾Ð³Ð¸Ð½"  required onChange={event => this.setStateWithEvent(event, "login")}/>
                            </Form.Group>
                            <Form.Group controlId="exampleForm.ControlTextarea1">
                                <Form.Control type="password" placeholder="Ð¿Ð°Ñ€Ð¾Ð»ÑŒ" required onChange={event => this.setStateWithEvent(event, "password1")}/>
                            </Form.Group>
                            <Form.Group controlId="exampleForm.ControlTextarea2">
                                <Form.Control type="password" placeholder="Ð¿Ð°Ñ€Ð¾Ð»ÑŒ ÐµÑ‰Ðµ Ñ€Ð°Ð·" required onChange={event => this.setStateWithEvent(event, "password2")}/>
                            </Form.Group>
                            <Form.Group controlId="formBasicName">
                                <Form.Control type="text" placeholder="Ð˜Ð¼Ñ"  required onChange={event => this.setStateWithEvent(event, "name")}/>
                            </Form.Group>
                            <Form.Group controlId="formBasicSurname">
                                <Form.Control type="text" placeholder="Ð¤Ð°Ð¼Ð¸Ð»Ð¸Ñ" required onChange={event => this.setStateWithEvent(event, "Surname")}/>
                            </Form.Group>
                            <Form.Group controlId="formBasicPatronymic">
                                <Form.Control type="text" placeholder="ÐžÑ‚Ñ‡ÐµÑÑ‚Ð²Ð¾" required onChange={event => this.setStateWithEvent(event, "Patronymic")}/>
                            </Form.Group>
                            <Form.Group>
                                <label htmlFor="exampleControlsFile1">Ð’Ñ‹Ð±Ð¸Ñ€Ð¸Ñ‚Ðµ ÐÐ²Ð°Ñ‚Ð°Ñ€</label>
                                <Form.File required id="exampleControlsFile1" onChange={(e:any)=>{
                                    this.uploadImage(e)
                                }}/>
                            </Form.Group>
                            <Form.Group controlId="formBasicTel">
                                <Form.Control  type="text" required minLength={11} maxLength={11} placeholder="Ð¢ÐµÐ»ÐµÑ„Ð¾Ð½"  onChange={event => this.setStateWithEvent(event, "tel")}/>

                            </Form.Group>

                            <Button  variant="primary" disabled={isInvalid} type="submit" block>
                                Ð—Ð°Ñ€ÐµÐ³Ð¸ÑÑ‚Ñ€Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒÑÑ
                            </Button>
                            <Button href="/signin" variant="outline-dark"  block>ÐÐ²Ñ‚Ð¾Ñ€Ð¸Ð·Ð¾Ð²Ð°Ñ‚ÑŒÑÑ</Button>


                        </Form>

                    </div>
                    <div className={'col-md-3'}>
                    </div>
                </div>
            </div>
                <BigFooter/>
            </>
        );
    }

    private uploadImage = async (e:any)=>{
        const file = e.target.files[0]
        const base64 = await this.convertBase64(file);
        this.setState({
            avatar:base64
        })
        console.log(this.state.avatar)
    }

    private convertBase64 = (file:any):Promise<string|null|ArrayBuffer> => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);

            fileReader.onload = () => {
                resolve(fileReader.result);
            };

        });
    };

    private setStateWithEvent(event: any, columnType: string): void {
        this.setState(SignUpForm.propKey(columnType, (event.target as any).value));
    }
}